.App-header {
  background-color: #e2d3a8;
  padding: 10px;
  text-align: center;
  font-size: 20px;
  color: black;
  line-height: 1;
}

.App-header > h1 {
  letter-spacing: 5px;
}

.App-header img {
  display: inline-block;
  width: 20px;
  margin: 0 0.5em 0.2em 0;
  vertical-align: middle;
}

.MainContent {
  display: flex;
  flex-direction: row;
  text-align: center;
}
@media (max-width:800px) {
  .MainContent {
    flex-direction: column;
  };
}

.education {
  min-width: 20%;
  background-color: rgb(191, 243, 243);
  padding: 0 10px 0;
}

.experience {
  padding: 10px;
  background-color:burlywood;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  min-width: 20%;
  padding: 10px;
  background-color: rgb(124, 194, 196);
}

.summary {
  padding: 5px;
  text-align: center;
  background-color: lightgray;
}

.App-link {
  color: #61dafb;
}
